        position: relative!important;
        flex-direction: column!important;
    }
    .success > div:first-of-type{
        height: 70vh!important;
        width: 100%!important;
        border-radius: 0!important;
    }
    .trans{
        width: 100%!important;
        height: 70vh!important;
    }
}