.heading{
    position: relative;
}
.heading:after{
    content: "";
    position: absolute;
    top: 180%;
    left: 0;
    width: 50%;
    height: 3px;
    background-color: #207FDC;
}
.book{
    position: absolute;
    top: 7rem;
    right: 10%;
    z-index: 1;
}
@media all and (min-width: 320px) and (max-width: 768px){
    .book{
        position: relative!important;
        top: 3rem;
        right: 0;
        width: 100%;
    }
}