@media all and (min-width: 320px) and (max-width: 768px) {
  .boxParent{
    margin-top: 0!important;
  }
  .box {
    margin: 0!important;
    width: 100%;
  }
  .list {
    flex-direction: column;
    position: static!important;
  }
  .list > li {
    border: 1px solid #fff;
    margin-top: 1rem;
    padding: 1rem 0;
  }
  .box{
      height: 100%!important;
  }
}
