.footerItems{
    position: relative;
}
.footerItems::after{
    content: "";
    position: absolute;
    bottom: 20%;
    left: 0;
    height: 1px;
    width: 100%;
    background-color: #fff;
}
@media all and (min-width: 320px) and (max-width: 768px){
    .footerItems{
        text-align: center!important;
    }
    .footerItems::after{    
        bottom: 8%;
    }
    .important{
        padding-left: 0!important;
    }
    .send{
        justify-content: center!important;
    }
} 