@media all and (min-width: 320px) and (max-width: 480px){
    .boxslider{
        flex-direction: column!important;
    }
    .boxslider > a{
        width: 100%!important;
        margin-top: 2rem;
    }
}
@media all and (min-width: 481px) and (max-width: 768px){
    .boxslider{
        flex-wrap: wrap!important;
    }
    .boxslider > a{
        width: 45%!important;
        margin-top: 2rem;
    }
}